//vue   vue transition  css3  transition  Jqeury toggle
//  css  transition: height .2s ease-in-out;
//

let on = {
    beforeEnter(el) {
        el.style.height = 0
        el.style.opacity = 0.1
    },
    enter(el) {
        if (el.scrollHeight !== 0) {
            el.style.height = el.scrollHeight + 'px'
            el.style.opacity = 1
        } else {
            el.style.height = ''
            el.style.opacity = ''
        }
    },
    afterEnter(el) {
        el.style.height = ''
        el.style.opacity = ''
    },

    beforeLeave(el) {
        el.style.height = el.scrollHeight + 'px'
        el.style.opacity = 1
    },
    leave(el) {
        if (el.scrollHeight !== 0) { el.style.height = 0; el.style.opacity = 0.1 }
    },
    afterLeave(el) {
        el.style.height = ''
        el.style.opacity = ''
    },
}

// https://cn.vuejs.org/v2/guide/render-function.html#

export default {
    name: 'Collapse',
    functional: true, //true   data   this
    render(h, context) {
        return h('transition', { on: on }, context.children)
    }
}


